/*  clients  */

.clients {
    @include list();
    @include nColumnList(2);

    @media screen and (min-width:768px) {
        @include nColumnList(3);
    }

    @media screen and (min-width:1200px) {
        @include nColumnList(6);
    }

    .clients__item {
        height: 92px;
    }
}

.client__link {
    @include centeredFlex();
    height: 100%;
    border: 1px solid #afb1b8;
    border-radius: 4px;
    fill: #afb1b8;
    transition: fill 250ms $timing-function, border-color 250ms $timing-function;

    &:hover,
    &:focus {
        fill: getColor('accent');
        border-color: getColor('accent');
    }
}

.client__icon {
    width: 106px;
    height: 60px;
}