/* sections */

.section {
    padding: 60px 0;
    text-align: center;

    @media screen and (min-width:1200px) {
        padding: 94px 0;
    }

    &__title {
        margin-bottom: $prime-gap;
        @include font(.84px, 28px);
        color: getCcolor('primary');

        // margin-bottom: 50px;
        // @include font(1.08px, 36px);
        // color: getCcolor('primary');
    }

    &__subtitle {
        margin-bottom: 10px;
        font-size: 14px;
        line-height: 1.15;
        text-transform: uppercase;
    }

    &__text {
        color: getColor('secondary');
    }
}