/* deal section */

.section--view {
    display: none;

    @media screen and (min-width: 1200px) {
        display: block;
    }
}

.deals {
    @include list();
    @include nColumnList(3)
}

.deals__item {
    position: relative;
    bottom: 0;
}

.deal__text {
    position: absolute;
    @include centeredFlex();
    bottom: 0;
    width: 100%;
    height: 70px;
    color: getColor('light');
    text-align: center;
    line-height: 1.15;
    text-transform: uppercase;
    background: rgba(47, 48, 58, 0.80);
}