.btn-list {
    @include list();
    display: flex;
    flex-wrap: wrap;
    gap: 15px 8px;
    padding-right: 15px;
    margin-bottom: 40px;

    @media screen and (min-width:768px) {
        @include centeredFlex();
    }
}

.btn {
    cursor: pointer;
    padding: 6px 22px;
    font-weight: 500;

    @include font($lineHeight: calc(26 / 16));
    border: none;
    border-radius: 4px;
    color: getColor('primary');
    background: getColor('background');
    transition: color 250ms $timing-function, box-shadow 250ms $timing-function, background-color 250ms $timing-function;

    &:hover,
    &:focus {
        color: #ffffff;
        box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.12),
            0px 1px 2px 0px rgba(0, 0, 0, 0.8), 0px 3px 1px 0px rgba(0, 0, 0, 0.10);
        background-color: getColor('accent');
    }
}